.c-layer-popup {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		overflow: hidden;
		outline: 0;
		background: rgba(0, 0, 0, 0.6);
		visibility: hidden;
		opacity: 0;
		z-index: -1;
		display: flex;
		justify-content: center;
		align-items: center;
		transition: opacity 0s ease-in-out;
}
.c-layer-popup .buttons {
		justify-content: flex-end;
}
.c-layer-popup .buttons .c-btn {
		min-width: 80px;
}
.c-layer-popup .c-tab-field .c-tab-list > li a {
		background-color: #fff;
}
.c-layer-popup .c-article {
		background-color: #fcfcfc;
}
.c-layer-popup.show {
		visibility: visible;
		z-index: 2000;
		opacity: 1;
}
.c-layer-popup.show .modal-field {
		top: 0;
		opacity: 1;
		transition: all 0.2s ease-in-out;
}

.modal-wrap {
		display: flex;
		align-items: center;
		max-height: calc(100% - 80px);
		flex-direction: column;
		justify-content: center;
		height: 100%;
}
.modal-wrap::before {
		content: none;
}

.modal-field {
		position: relative;
		width: 100%;
		border: 1px solid #e9e9e9;
		box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
		border-radius: 12px;
		background-color: #fff;
		z-index: 2001;
		overflow: hidden;
		color: #494949;
		display: flex;
		flex-direction: column;
		justify-content: center;
		top: -100px;
		opacity: 0;
		transition: all 0.3s ease-out;
		max-height: none;
}

.c-modal-body.co-modal-scroll {
		overflow-x: auto;
		margin-bottom: 10px;
}
.c-modal-body.co-modal-scroll .c-modal-inner {
		padding-bottom: 0;
}
.c-modal-body.co-modal-scroll .co-table-field {
		overflow: initial;
}

.c-modal-header {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		position: relative;
		border: 0;
		border-radius: 0;
		flex-shrink: 0;
		padding: 32px 40px 20px;
}
.c-modal-header .c-modal-title-field {
		width: 100%;
}
.c-modal-header .c-modal-title-field .c-title {
		margin-right: 40px;
		text-align: left;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		word-wrap: break-word;
		font-size: 24px;
		font-weight: 700;
		line-height: 1.5;
		color: #253981;
}
.c-modal-header .popup-cls-btn {
		position: absolute;
		top: 32px;
		right: 40px;
		z-index: 10;
		background: url("../images/ico/ico_close_01.svg") center/28px no-repeat;
		width: 28px;
		height: 28px;
		opacity: 1;
		transition: opacity 0.4s;
}
.c-modal-header .popup-cls-btn:hover {
		transition: opacity 0.4s;
		opacity: 0.6;
}

.c-modal-body {
		margin: 0 40px;
		position: relative;
		overflow-x: hidden;
		overflow-y: auto;
}
.c-modal-body::-webkit-scrollbar {
		width: 8px;
		height: 8px;
}
.c-modal-body::-webkit-scrollbar-track {
		background-color: #fff;
}
.c-modal-body::-webkit-scrollbar-thumb {
		background-color: #fff;
		border-radius: 3px;
		transition: background-color 1s;
}
.c-modal-body:hover {
		transition: background-color 1s;
}
.c-modal-body:hover::-webkit-scrollbar-thumb {
		background-color: #e9e9e9;
}
.c-modal-body .cont-box {
		color: #777676;
}
.c-modal-body .cont-box .title {
		font-size: 16px;
		font-weight: 500;
		line-height: 2;
		color: #1c1c1b;
}
.c-modal-body .cont-box .cont {
