}
.c-modal-body .c-modal-inner {
		padding-bottom: 20px;
}
.c-modal-body .c-sub-title {
		font-size: 20px;
		font-weight: 500;
		line-height: 1.8;
		color: #253981;
}
.c-modal-body .c-modal-middle {
		margin-bottom: 30px;
}

.c-modal-footer {
		padding: 0 40px 32px;
		flex-shrink: 0;
}
.c-modal-footer .inner {
		align-items: center;
		padding-top: 20px;
		border-top: 1px solid #e9e9e9;
		display: flex;
		width: 100%;
		justify-content: flex-end;
		flex-shrink: 0;
}
.c-modal-footer .buttons .co-btn {
		width: auto;
		min-width: 80px;
		border-radius: 12px;
		position: relative;
		overflow: hidden;
}
.c-modal-footer .buttons .co-btn:not(:disabled):after {
		background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0, rgba(255, 255, 255, 0.493) 50%, rgba(255, 255, 255, 0) 100%);
		content: "";
		height: 200%;
		left: -80%;
		opacity: 0.2;
		position: absolute;
		top: 50%;
		transform: rotate(30deg) translateY(-50%);
		width: 20px;
		z-index: -10;
