		position: fixed;
		background-color: #253981;
		top: 0;
		left: 0;
		height: 100%;
		z-index: 500;
}
#sideNav .sidenav-wrapper {
		display: flex;
		flex-direction: column;
		overflow: hidden;
}
#sideNav .sidebar-wrap {
		height: 100%;
		overflow: hidden;
}
#sideNav .top-logo {
		height: 80px;
		min-height: 80px;
		padding: 16px 28px 16px 20px;
		border-bottom: 1px solid #3b4e93;
}
#sideNav .top-logo .logo {
		position: relative;
		height: 100%;
		display: flex;
}
#sideNav .top-logo .logo > a {
		display: flex;
		align-items: center;
		padding: 8px 0;
		width: 100%;
		height: 48px;
		line-height: 1;
		overflow: hidden;
		text-align: center;
}
#sideNav .top-logo .logo img {
		height: auto;
		max-height: 100%;
		max-width: 100%;
}
#sideNav .top-logo .logo.type-color a {
		background-color: #fff;
		border-radius: 16px;
		justify-content: center;
		padding-left: 8px;
		padding-right: 8px;
}
.sidebar-menu {
		height: 100%;
		overflow-y: auto;
}
.sidebar-menu::-webkit-scrollbar {
		opacity: 0;
		height: 8px;
		width: 8px;
		background-color: #fff;
}
.sidebar-menu::-webkit-scrollbar:hover {
		opacity: 1;
}
.sidebar-menu ::-webkit-scrollbar-track {
		background-color: #fff;
}
.sidebar-menu::-webkit-scrollbar-thumb {
		opacity: 0;
		background: #e9e9e9;
		border-radius: 50px;
		background-clip: padding-box;
}
.sidebar-menu::-webkit-scrollbar-corner {
		opacity: 0;
		background-color: transparent;
}
.sidebar-menu li a {
		color: rgba(255, 255, 255, 0.8);
		position: relative;
}
.sidebar-menu > li > a {
		padding: 14px 20px;
		color: rgba(255, 255, 255, 0.8);
		display: flex;
		align-items: center;
		font-size: 16px;
		font-weight: 400;
		line-height: 2;
}
.sidebar-menu > li > a:after {
		position: absolute;
		top: 50%;
		right: 10px;
		background: url("../images/ico/ico_arrow_03.svg") right center/100% no-repeat;
		content: "";
		display: inline-block;
		width: 28px;
		height: 28px;
		transition: transform 0.4s;
		transform: translateY(-50%) rotate(0deg);
}
.sidebar-menu > li > a:only-child:after {
		display: none;
}
.sidebar-menu > li > a.on {
		color: #253981;
		background-color: #fff;
}
.sidebar-menu > li > a.on:after {
		background-image: url("../images/ico/ico_arrow_11.svg");
		transition: transform 0.4s;
		transform: translateY(-50%) rotate(-360deg);
}
.sidebar-menu > li > a.on > .s-menu:before {
		background-position-y: -38px;
}
.sidebar-menu > li > a.on .badge:hover {
		box-shadow: none;
}
.sidebar-menu .s-menu {
		position: relative;
		padding-left: 34px;
		vertical-align: middle;
}
.sidebar-menu .s-menu:before {
		display: inline-block;
		position: absolute;
		top: 0;
		left: 0;
		content: "";
		width: 28px;
		height: 28px;
		background-image: url("../images/ico/ico_sprites_01.svg");
		background-position-y: 0;
		background-size: auto 218px;
}
.sidebar-menu .s-menu.nav01:before {
		background-position-x: -456px;
}
.sidebar-menu .s-menu.nav02:before {
		background-position-x: -494px;
}
.sidebar-menu .s-menu.nav03:before {
		background-position-x: -532px;
}
.sidebar-menu .s-menu.nav04:before {
		background-position-x: -570px;
}
.sidebar-menu .s-menu.nav05:before {
		background-position-x: -608px;
}
.sidebar-menu .s-menu.nav06:before {
		background-position-x: -1216px;
}
.sidebar-menu .badge {
		margin-top: 1px;
		margin-left: 12px;
		display: inline-flex;
		font-size: 0;
		line-height: 12px;
		transition: box-shadow 0.5s;
}
.sidebar-menu .badge > em {
		padding: 0 8px;
		display: inline-block;
		border-radius: 4px;
		background-color: #ff6859;
		font-size: 12px;
		font-weight: 500;
		line-height: 1.6666666667;
		text-align: center;
		color: #fff;
		font-style: normal;
		position: relative;
}
.sidebar-menu .badge > em:before {
		content: "";
		position: absolute;
		top: 4px;
		left: -7px;
		border-top: 6px solid transparent;
		border-bottom: 6px solid transparent;
		border-right: 8px solid #ff6859;
}
.sidebar-menu .badge:hover {
		transition: box-shadow 0.5s;
		box-shadow: 0 0 10px 2px rgba(255, 255, 255, 0.25);
}

.sidebar-sub-menu {
		display: none;
		overflow: hidden;
		padding-top: 12px;
		padding-bottom: 12px;
}
.sidebar-sub-menu > li > a {
		padding: 12px 24px;
		display: inline-block;
		position: relative;
		color: rgba(255, 255, 255, 0.6);
}
.sidebar-sub-menu > li > a.on {
		color: #fff;
}
.sidebar-sub-menu > li > a.on:before {
		background-color: #fff;
}

/*
= Header
*/
.header {
		height: 80px;
		background-color: #fff;
		width: 100%;
		position: fixed;
		padding-left: 260px;
		top: 0;
		z-index: 100;
		border-bottom: 1px solid #e9e9e9;
		left: 0;
}
.header .wrap {
		display: flex;
		padding: 16px 60px;
		justify-content: space-between;
		width: 100%;
		align-items: center;
		max-width: 2300px;
}
.header .top-left,
.header .top-right {
		display: flex;
		flex: 0 0 auto;
		align-items: center;
}
.header .top-right .user-info {
		display: flex;
		align-items: center;
}
.header .top-right .user-info > li {
		vertical-align: middle;
		padding-left: 20px;
		color: #777676;
}
.header .slogan {
		font-size: 20px;
		font-weight: 500;
		line-height: 1.8;
		color: #494949;
}
.header .user-profile {
		position: relative;
		border-radius: 20px;
		margin-left: 30px;
		background-color: #253981;
}
.header .user-profile > a {
		opacity: 1;
		padding: 8px 20px 8px 16px;
		transition: opacity 0.3s;
		display: flex;
		align-items: center;
		border-radius: 20px;
		text-align: center;
}
.header .user-profile > a:hover {
		opacity: 0.8;
}
.header .user-profile .name {
		font-size: 16px;
		font-weight: 500;
		line-height: 2;
		color: rgba(255, 255, 255, 0.6);
		padding-left: 12px;
}
.header .user-profile .name > em {
		color: #fff;
}
.header .user-profile .img {
		width: 24px;
		height: 24px;
		border-radius: 50%;
		overflow: hidden;
}
.header .btn-area {
		float: left;
}
.header .btn-area:after {
		display: block;
		content: "";
		clear: both;
}
.header .user-control {
		width: 135px;
		position: absolute;
		z-index: 100;
		top: 49px;
		left: 0;
		border-radius: 8px;
		background-color: #fff;
		box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.25);
		border: 1px solid #e9e9e9;
		transform: scale(1, 0);
		transition: transform 0.1s ease-in;
		transform-origin: 0 0;
}
.header .user-control > ul {
		opacity: 0;
}
.header .user-control > ul > li ~ li {
		border-top: 1px solid #e9e9e9;
}
.header .user-control > ul > li a,
.header .user-control > ul > li button {
		display: block;
		padding: 11px 20px;
		color: #494949;
		font-size: 14px;
		font-weight: 300;
		line-height: 2.1428571429;
}
.header .user-control.on {
		transform: scale(1, 1);
		transition: transform 0.1s ease-in;
}
.header .user-control.on > ul {
		opacity: 1;
}

